import {formatDate} from '@/Api/dealDate.js'
export default{
	data(){
		return{
			type:-1,//0支付成功  1预约成功
			windowHeight:0,//屏幕高度
			orderInfo:[]
		}
	},
	onLoad(e){
		console.log(e)
		var that = this
		that.type = e.id
		if(that.type == 0){
			uni.setNavigationBarTitle({
				title:'支付成功'
			})
		}else{
			uni.setNavigationBarTitle({
				title:'预约成功'
			})
		}	
		
		uni.getSystemInfo({
		    success: function (res) {
				that.windowHeight = res.windowHeight
				
		    }
		});
		// 获取全局变量order
		this.orderInfo = getApp().globalData.orderInfo
		console.log(this.orderInfo)
	},
	computed:{
		backButton(){
			return this.type == 0 ? '返回个人中心' : '查看预约记录'
		}
	},
	methods:{
		succeedClick(){
			if(this.type == 0){
				uni.switchTab({
					url:'../../My/my'
				})
			}else{
				uni.redirectTo({
					url:'../../My/appointmentDetail/appointmentdetail'
				})
			}
		},
	}
}
